'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { DashboardNav } from '@/components/dashboard-nav';
import { PaymentForm } from '@/components/payment-form';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { authService } from '@/lib/auth';
import { paymentsStorage, projectsStorage, clientsStorage } from '@/lib/storage';
import { Payment, Project, Client } from '@/types';
import { toast } from 'sonner';

// Extended type for payments with project details
type PaymentWithProject = Payment & {
  project?: (Project & { client?: Client }) | undefined;
};

export default function PaymentsPage() {
  const router = useRouter();
  const [payments, setPayments] = useState<PaymentWithProject[]>([]);
  const [projects, setProjects] = useState<Project[]>([]);
  const [clients, setClients] = useState<Client[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const [isPaymentModalOpen, setIsPaymentModalOpen] = useState(false);

  useEffect(() => {
    // Check authentication
    if (!authService.isAuthenticated()) {
      router.push('/login');
      return;
    }

    loadPayments();
  }, [router]);

  const loadPayments = () => {
    try {
      const paymentsData = paymentsStorage.load();
      const projectsData = projectsStorage.load();
      const clientsData = clientsStorage.load();
      
      // Add project and client info to payments
      const paymentsWithDetails = paymentsData.map(payment => {
        const project = projectsData.find(p => p.id === payment.projectId);
        const client = project ? clientsData.find(c => c.id === project.clientId) : undefined;
        
        return {
          ...payment,
          project: project ? { ...project, client } : undefined
        };
      });

      setPayments(paymentsWithDetails);
      setProjects(projectsData);
      setClients(clientsData);
    } catch (error) {
      console.error('Error loading payments:', error);
      toast.error('Error al cargar pagos');
    } finally {
      setIsLoading(false);
    }
  };

  const filteredPayments = payments.filter(payment =>
    payment.project?.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
    payment.project?.client?.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    payment.method?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const getStatusBadge = (status: Payment['status']) => {
    const statusConfig = {
      'pending': { label: 'Pendiente', variant: 'secondary' as const },
      'paid': { label: 'Pagado', variant: 'outline' as const },
      'overdue': { label: 'Vencido', variant: 'destructive' as const }
    };

    const config = statusConfig[status];
    return (
      <Badge 
        variant={config.variant} 
        className={status === 'paid' ? 'bg-green-100 text-green-800 border-green-200' : ''}
      >
        {config.label}
      </Badge>
    );
  };

  const formatCLP = (amount: number) => {
    return new Intl.NumberFormat('es-CL', {
      style: 'currency',
      currency: 'CLP',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0
    }).format(amount);
  };

  const getMethodLabel = (method: Payment['method']) => {
    const methodLabels = {
      'cash': 'Efectivo',
      'transfer': 'Transferencia',
      'paypal': 'PayPal',
      'stripe': 'Stripe',
      'other': 'Otro'
    };
    return method ? methodLabels[method] : 'No especificado';
  };

  const getTotalAmount = () => {
    return filteredPayments.reduce((sum, payment) => sum + payment.amount, 0);
  };

  const getPaidAmount = () => {
    return filteredPayments
      .filter(payment => payment.status === 'paid')
      .reduce((sum, payment) => sum + payment.amount, 0);
  };

  const getPendingAmount = () => {
    return filteredPayments
      .filter(payment => payment.status === 'pending' || payment.status === 'overdue')
      .reduce((sum, payment) => sum + payment.amount, 0);
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-slate-200 border-t-slate-600 rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-slate-600">Cargando pagos...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-slate-50">
      <DashboardNav />
      
      <div className="md:ml-64">
        <div className="p-6">
          {/* Header */}
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between mb-8">
            <div>
              <h1 className="text-3xl font-bold text-slate-900">Pagos</h1>
              <p className="text-slate-600 mt-2">
                Seguimiento de pagos y facturación
              </p>
            </div>
            
            <Button 
              onClick={() => setIsPaymentModalOpen(true)}
              className="mt-4 sm:mt-0"
            >
              Registrar Pago
            </Button>
          </div>

          {/* Summary Cards */}
          {filteredPayments.length > 0 && (
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
              <Card>
                <CardContent className="p-4">
                  <div className="text-sm font-medium text-slate-500">Total</div>
                  <div className="text-2xl font-bold text-slate-900">
                    {formatCLP(getTotalAmount())}
                  </div>
                </CardContent>
              </Card>
              
              <Card>
                <CardContent className="p-4">
                  <div className="text-sm font-medium text-slate-500">Pagado</div>
                  <div className="text-2xl font-bold text-green-600">
                    {formatCLP(getPaidAmount())}
                  </div>
                </CardContent>
              </Card>
              
              <Card>
                <CardContent className="p-4">
                  <div className="text-sm font-medium text-slate-500">Pendiente</div>
                  <div className="text-2xl font-bold text-orange-600">
                    {formatCLP(getPendingAmount())}
                  </div>
                </CardContent>
              </Card>
            </div>
          )}

          {/* Search */}
          <div className="mb-6">
            <Input
              placeholder="Buscar pagos por proyecto, cliente o método..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="max-w-md"
            />
          </div>

          {/* Payments List */}
          {filteredPayments.length === 0 ? (
            <Card>
              <CardContent className="flex flex-col items-center justify-center py-12">
                <div className="w-16 h-16 bg-slate-100 rounded-full flex items-center justify-center mb-4">
                  <div className="w-8 h-8 bg-slate-300 rounded"></div>
                </div>
                <h3 className="text-lg font-semibold text-slate-900 mb-2">
                  {searchTerm ? 'No se encontraron pagos' : 'No tienes pagos registrados'}
                </h3>
                <p className="text-slate-500 text-center mb-4">
                  {searchTerm 
                    ? 'Intenta con otros términos de búsqueda'
                    : 'Comienza registrando tu primer pago'
                  }
                </p>
                {!searchTerm && (
                  <Button onClick={() => setIsPaymentModalOpen(true)}>
                    Registrar Primer Pago
                  </Button>
                )}
              </CardContent>
            </Card>
          ) : (
            <div className="space-y-4">
              <p className="text-sm text-slate-600">
                {filteredPayments.length} pago{filteredPayments.length !== 1 ? 's' : ''}
              </p>
              
              <div className="grid gap-4">
                {filteredPayments.map((payment) => (
                  <Card key={payment.id} className="hover:shadow-md transition-shadow">
                    <CardContent className="p-6">
                      <div className="flex items-start justify-between mb-4">
                        <div className="flex-1">
                          <h3 className="text-lg font-semibold text-slate-900 mb-1">
                            {payment.project?.title || 'Proyecto eliminado'}
                          </h3>
                          <p className="text-sm text-slate-600">
                            Cliente: {payment.project?.client?.name || 'Sin cliente'}
                          </p>
                        </div>
                        <div className="flex items-center space-x-2">
                          {getStatusBadge(payment.status)}
                          <div className="text-right">
                            <div className="text-lg font-bold text-slate-900">
                              {formatCLP(payment.amount)}
                            </div>
                            <div className="text-sm text-slate-500">
                              {payment.currency}
                            </div>
                          </div>
                        </div>
                      </div>
                      
                      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm mb-4">
                        <div>
                          <span className="text-slate-500">Vencimiento:</span>
                          <p className="font-medium">
                            {new Date(payment.dueDate).toLocaleDateString('es-CL')}
                          </p>
                        </div>
                        
                        {payment.paidDate && (
                          <div>
                            <span className="text-slate-500">Fecha de Pago:</span>
                            <p className="font-medium text-green-600">
                              {new Date(payment.paidDate).toLocaleDateString('es-CL')}
                            </p>
                          </div>
                        )}
                        
                        <div>
                          <span className="text-slate-500">Método:</span>
                          <p className="font-medium">
                            {getMethodLabel(payment.method)}
                          </p>
                        </div>
                        
                        <div>
                          <span className="text-slate-500">Creado:</span>
                          <p className="font-medium">
                            {new Date(payment.createdAt).toLocaleDateString('es-CL')}
                          </p>
                        </div>
                      </div>
                      
                      {payment.notes && (
                        <div className="mb-4">
                          <span className="text-sm text-slate-500">Notas:</span>
                          <p className="text-sm text-slate-700 mt-1">
                            {payment.notes}
                          </p>
                        </div>
                      )}
                      
                      <div className="flex items-center justify-between pt-4 border-t border-slate-100">
                        <div className="text-xs text-slate-500">
                          ID: {payment.id.slice(0, 8)}...
                        </div>
                        
                        <div className="flex space-x-2">
                          {payment.status === 'pending' && (
                            <Button
                              variant="outline"
                              size="sm"
                              onClick={() => toast.info('Funcionalidad próximamente')}
                            >
                              Marcar como Pagado
                            </Button>
                          )}
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => toast.info('Funcionalidad próximamente')}
                          >
                            Editar
                          </Button>
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => toast.info('Funcionalidad próximamente')}
                          >
                            Eliminar
                          </Button>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Payment Form Modal */}
      <PaymentForm
        isOpen={isPaymentModalOpen}
        onClose={() => setIsPaymentModalOpen(false)}
        onPaymentAdded={() => {
          loadPayments();
        }}
      />
    </div>
  );
}
