'use client';

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from '@/components/ui/dialog';
import { paymentsStorage, projectsStorage, clientsStorage, generateId } from '@/lib/storage';
import { Payment, Project, Client } from '@/types';
import { toast } from 'sonner';

interface PaymentFormProps {
  isOpen: boolean;
  onClose: () => void;
  onPaymentAdded: () => void;
}

export function PaymentForm({ isOpen, onClose, onPaymentAdded }: PaymentFormProps) {
  const [projects, setProjects] = useState<(Project & { client?: Client })[]>([]);
  const [formData, setFormData] = useState({
    projectId: '',
    amount: '',
    currency: 'CLP',
    status: 'pending' as Payment['status'],
    dueDate: '',
    method: undefined as Payment['method'],
    notes: ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    if (isOpen) {
      loadProjects();
      // Reset form when opening
      setFormData({
        projectId: '',
        amount: '',
        currency: 'CLP',
        status: 'pending',
        dueDate: '',
        method: undefined,
        notes: ''
      });
    }
  }, [isOpen]);

  const loadProjects = () => {
    try {
      const projectsData = projectsStorage.load();
      const clientsData = clientsStorage.load();
      
      const projectsWithClients = projectsData.map(project => {
        const client = clientsData.find(c => c.id === project.clientId);
        return { ...project, client };
      });
      
      setProjects(projectsWithClients);
    } catch (error) {
      console.error('Error loading projects:', error);
      toast.error('Error al cargar proyectos');
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!formData.projectId || !formData.amount || !formData.dueDate) {
      toast.error('Por favor completa todos los campos requeridos');
      return;
    }

    const amount = parseFloat(formData.amount);
    if (isNaN(amount) || amount <= 0) {
      toast.error('El monto debe ser un número válido mayor a 0');
      return;
    }

    setIsSubmitting(true);

    try {
      const newPayment: Payment = {
        id: generateId(),
        projectId: formData.projectId,
        amount: amount,
        currency: formData.currency,
        status: formData.status,
        dueDate: new Date(formData.dueDate),
        method: formData.method || undefined,
        notes: formData.notes || undefined,
        createdAt: new Date(),
        updatedAt: new Date()
      };

      // If status is paid, set paidDate to today
      if (formData.status === 'paid') {
        newPayment.paidDate = new Date();
      }

      paymentsStorage.add(newPayment);
      
      toast.success('Pago registrado exitosamente');
      onPaymentAdded();
      onClose();
    } catch (error) {
      console.error('Error saving payment:', error);
      toast.error('Error al guardar el pago');
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleInputChange = (field: string, value: string | undefined) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[500px]">
        <DialogHeader>
          <DialogTitle>Registrar Nuevo Pago</DialogTitle>
        </DialogHeader>
        
        <form onSubmit={handleSubmit} className="space-y-4">
          {/* Project Selection */}
          <div className="space-y-2">
            <Label htmlFor="project">Proyecto *</Label>
            <Select value={formData.projectId} onValueChange={(value) => handleInputChange('projectId', value)}>
              <SelectTrigger>
                <SelectValue placeholder="Selecciona un proyecto" />
              </SelectTrigger>
              <SelectContent>
                {projects.map((project) => (
                  <SelectItem key={project.id} value={project.id}>
                    {project.title} - {project.client?.name || 'Sin cliente'}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          {/* Amount */}
          <div className="space-y-2">
            <Label htmlFor="amount">Monto *</Label>
            <div className="flex space-x-2">
              <Input
                id="amount"
                type="number"
                placeholder="0"
                value={formData.amount}
                onChange={(e) => handleInputChange('amount', e.target.value)}
                className="flex-1"
                min="0"
                step="1"
              />
              <Select value={formData.currency} onValueChange={(value) => handleInputChange('currency', value)}>
                <SelectTrigger className="w-20">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="CLP">CLP</SelectItem>
                  <SelectItem value="USD">USD</SelectItem>
                  <SelectItem value="EUR">EUR</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>

          {/* Due Date */}
          <div className="space-y-2">
            <Label htmlFor="dueDate">Fecha de Vencimiento *</Label>
            <Input
              id="dueDate"
              type="date"
              value={formData.dueDate}
              onChange={(e) => handleInputChange('dueDate', e.target.value)}
            />
          </div>

          {/* Status */}
          <div className="space-y-2">
            <Label htmlFor="status">Estado</Label>
            <Select value={formData.status} onValueChange={(value) => handleInputChange('status', value as Payment['status'])}>
              <SelectTrigger>
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="pending">Pendiente</SelectItem>
                <SelectItem value="paid">Pagado</SelectItem>
                <SelectItem value="overdue">Vencido</SelectItem>
              </SelectContent>
            </Select>
          </div>

          {/* Payment Method */}
          <div className="space-y-2">
            <Label htmlFor="method">Método de Pago</Label>
            <Select value={formData.method || ''} onValueChange={(value) => handleInputChange('method', value === '' ? undefined : value)}>
              <SelectTrigger>
                <SelectValue placeholder="Selecciona método" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="cash">Efectivo</SelectItem>
                <SelectItem value="transfer">Transferencia</SelectItem>
                <SelectItem value="paypal">PayPal</SelectItem>
                <SelectItem value="stripe">Stripe</SelectItem>
                <SelectItem value="other">Otro</SelectItem>
              </SelectContent>
            </Select>
          </div>

          {/* Notes */}
          <div className="space-y-2">
            <Label htmlFor="notes">Notas</Label>
            <Textarea
              id="notes"
              placeholder="Notas adicionales..."
              value={formData.notes}
              onChange={(e) => handleInputChange('notes', e.target.value)}
              rows={3}
            />
          </div>

          <DialogFooter>
            <Button type="button" variant="outline" onClick={onClose} disabled={isSubmitting}>
              Cancelar
            </Button>
            <Button type="submit" disabled={isSubmitting}>
              {isSubmitting ? 'Guardando...' : 'Registrar Pago'}
            </Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  );
}
